@charset "UTF-8";
// 갤러리
.gallery {
    position:relative; width:100%; background-color:$navy;
    section {}
    .sec1 {
        position:relative; padding:280px 0 0; overflow:hidden;
        // slide
        #gallerySlide {
            width:1580px; margin:0 auto; overflow:visible;
            cursor:url(../../images/main/slide_cursor.png), auto;
            img {cursor:url(../../images/main/slide_cursor.png), auto; }
            &:before {
                width:100%; height:100%; content:""; display:block; background-color:$navy;
                position:absolute; top:0; right:1580px; z-index:100;
            }
            .gallery_slide {
                width:auto; position:relative;

            }
            .txt_box {
                position:absolute; bottom:54px; left:50px;
                @include transform(skew(-0.03deg));
            }
            span { display:block; color:$white; opacity:.6; font-size:14px; letter-spacing:1px; }
            strong { display:block; padding:15px 0 0; color:$white; font-size:16px; }
        }
        .top_txt { display:block; font-size:15px; color:#75777c; padding:0 0 153px; }
        h1 { position:absolute; top:47px; font-size:150px; z-index:2; color:$white; left:-6px; }
    }
    .sec2 {
        width:100%; background-color:$navy;
        img { width:103px; height:103px; padding:170px 0 0; }
        .txt_wrap {
            width:950px; margin:0 auto; text-align:center; padding:0 0 220px;
            p { padding:50px 0 0; color:$goldYellow; font-size:24px; }
            h1 { padding:30px 0 75px; font-size:50px; color:$white; }
            span {
                position:relative; color:$white; font-size:18px; display:block; width:fit-content; margin:0 auto;
                &:after, &:before { content:"-"; display:block; position:absolute; top:0; }
                &:after { right:-17px; }
                &:before { left:-17px; }
            }
            strong { display:block; padding:35px 0 0; font-size:72px; color:$goldYellow; }
        }
    }
}
.household {
    background-color:red;
}