@charset "UTF-8";
.main {
    overflow:hidden;
    section { width:100%; }
    .sec1 {
        height:768px; position:relative;
        .video_wrap {
            width:100%; height:100%; min-width:1400px; overflow:hidden;
            video {width:100%; height:auto;position:relative; z-index:-1;}
        }
        .txt_wrap {
            width:100%; max-width:1580px; min-width:1280px;
            padding:0 50px; position:absolute; left:50%; bottom:-55px;
            @include transform(translate(-50%, 0));
            h1 {
                width:fit-content; position:relative; font-size:160px; color:$white;
                letter-spacing:-5px;
                i { position:absolute; font-size:32px; font-weight:$regular; right:-165px; top:34px; letter-spacing:0; }
            }
            span {color:$white; font-size:30px;letter-spacing:-1px;}
        }
    }
    .sec2 {
        height:1090px; background:url(../../images/main/sec1_img.jpg) 50% 50% no-repeat; background-size:cover;
        .txt_wrap {
            width:fit-content; margin:0 auto; text-align:center; padding:245px 0 0;
            & > span {
                display:block;
                &:nth-of-type(1) { padding:50px 0 0; color:#d2ad83; font-size:24px;}
                &:nth-of-type(2) { padding:40px 0 60px; color:$white; font-size:20px; }
            }
            h1 { padding:85px 0 0; color:#f0f0f0; font-size:50px; }
            p {
                color:#acb4bf; font-size:16px; line-height:38px;
                span { display:block; @include transform(skew(-0.03deg)); }
                i { font-family:$NanumSquare; }
            }
        }
    }
    .sec3 {
        background-color:#f8f0ec;
        // slider
        .moving_slide {
            width:1280px; margin:0 auto; position:relative;
            .txt_wrap {
                left:710px; top:300px; font-family:$NanumSquare; position:relative; z-index:2;
                li {
                    position:absolute; opacity:0;
                    &:first-child { opacity:1; }
                }
                .txt_box {padding:20px 0 0 135px;}
                strong { display:block; font-size:120px ;color:#a8835b; font-family:$Play; }
                span { display:block; font-size:46px; color:$navy; font-weight:$extraBold; line-height:60px;  }
                p { padding:25px 0 0; font-size:18px; color:$navy; line-height:30px; font-weight:$bold;  }
            }
        }
        .slide_img {
            position:relative;
            ul {position:relative;@include boxSizing;}
            li {position:absolute; top:0; left:0;overflow:hidden;}
            .image_1 {width:580px; height:774px;margin:0 0 0 150px;}
            .image_2 {width:350px; height:294px;position:absolute;left:-320px; top:600px;}
            .image_3 {width:180px; height:234px;position:absolute;right:50px; top:50px;}
            .image_4 {width:330px;height:206px;margin:0 0 0 850px;}
            .image_5 {width:249px; height:250px;position:absolute;right:-320px; top:400px;}
        }
        .slide_btn {
            width:180px; height:80px;position:absolute; bottom:80px; left:150px;display:flex; justify-content:space-between;
            .prev_btn, .next_btn {
                width:80px; height:80px; background:url(../../images/main/slide_btn.png) 0 0 no-repeat;
                &:hover {background:url(../../images/main/slide_btn_over.png) 0 0 no-repeat;}
            }
            .next_btn {@include transform(rotate(180deg));}
        }




        // info
        .cont_guide {
            min-width:1180px; max-width:1580px; padding:0 50px; align-items:center;
            margin:0 auto; display:flex;
        }
        .overview {
            padding:155px 0 100px;
            .img_wrap {
                width:57%; overflow:hidden;
                img {
                    height:100%; object-fit:cover; position:relative; top:0; left:50%;
                    @include transform(translate(-50%,0));
                }
            }
            .txt_wrap {
                width:500px; font-family:$NanumSquare; padding:0 0 0 7%;
                @include transform(skew(-0.03deg));
                strong { line-height:72px; font-size:52px; font-weight:$bold; color:#a8835b; letter-spacing:-4px; }
                span { padding:60px 0 0; display:block; font-size:20px; font-weight:$extraBold; color:$navy; }
                p {
                    padding:35px 0 0; line-height:36px; color:$navy; font-size:16px; font-weight:$bold;
                    @include transform(skew(-0.03deg));
                    &:first-of-type { padding:20px 0 0; }
                }
            }
        }
    }
    // 가치
    .sec4 {
        height:873px; background-color:$navy; padding:0 0 180px;
        .cont_wrap {
            padding:60px 0 0; width:1580px; margin:0 auto; position:relative;
            .line {
                width:100%; height:1px; background-color:#30343a; position:absolute; top:143px; left:0;
                i { width:46px; height:1px; background-color:$white; display:block; position:relative; left:0; }
            }
        }
        .tab_menu {
            display:flex; justify-content:space-between; padding:0 155px 0 0; margin:0 0 0 -40px;
            li.active {
                button {
                    color:$goldYellow;
                    span { color:$white; }
                }
            }
            button {
                font-family:$NanumSquare; text-align:left; font-size:14px; color:#75777c; font-weight:$bold;
                padding:20px 40px;
                @include transition(all, .1s);
                @include transform(skew(-0.03deg));
            }
            span {
                font-family:$Play; padding:6px 0 0; display:block; letter-spacing:.7px; font-weight:$regular;
            }
            em { padding:0 0 0 7px; font-family:$Play; font-weight:$regular; }
        }
        .tab_wrap {
            width:100%; height:800px; position:absolute; top:144px;
            li {
                width:100%; position:absolute; left:0; top:0; display:none;
                &:first-child { display:block; opacity:1; }
            }
            .tab_cont {
                padding:80px 0 0;
                .txt_wrap {
                    & > span { color:$goldYellow; font-size:14px; }
                    & > strong {
                        padding:30px 0 0; display:block; font-size:54px; color:$goldYellow; font-weight:$regular;
                        letter-spacing:-3px;
                    }

                }
            }
            .cont1 {
                display:flex; justify-content:space-between; /* background-color:#191d24;*/
                .txt_wrap { width:100%; }
                .img_wrap { margin:0 -20px 0 0; }
                .explanation_box {
                    padding:40px 0 0;
                    @include transform(skew(-0.03deg));
                    strong {
                        display:block; font-size:20px; color:$white; padding:60px 0 17px;
                    }
                    p {
                        position:relative; padding:13px 0 0 15px; color:#bec5ce; font-size:16px; font-weight:$regular;
                        @include transform(skew(-0.03deg));
                        &:before { display:block; content:"-"; position:absolute; left:0; top:12px; }
                    }
                }
            }
        }
    }
    // 세대 선택하기
    .sec5 {
        width:100%; overflow:hidden; position:relative;
        .generation_type {
            .img_wrap {
                max-width:1920px; min-width:1280px;
                img { width:100%; }
            }
            .svg_wrap {
                max-width:1920px; min-width:1280px; position:absolute; top:0; left:0; z-index:2;
                svg { width:100%; height:auto; }
                path { fill:transparent; position:relative; z-index:10; }
            }
            .in_svg {
                width:100%; height:100%; max-width:1920px; min-width:1280px;
                position:absolute; top:0; left:0; mix-blend-mode:multiply; filter: contrast(125%);
                path {
                    fill:transparent;
                    @include transition(all, .4s);
                    &.active {fill:#977a5c;}
                }
            }
            .info_wrap {
                max-width:1920px; min-width:1280px; width:100%; height:100%; position:absolute; left:0; top:0; z-index:1;
                li {
                    position:absolute; z-index:1;
                    &.active {
                        z-index:2;
                        &:nth-of-type(1), &:nth-of-type(2), &:nth-of-type(3), &:nth-of-type(4), &:nth-of-type(5) {
                            &:after { opacity:1; }
                        }
                        .plus_btn {
                            em { opacity:1; @include transform(rotate(90deg)); }
                        }
                        .sub_info { opacity:1; display:block; @include transform(translate(0,-50%)); }
                    }
                    &:after {
                        width:100%; height:100%; content:""; display:block; position:absolute; top:0; left:0; opacity:0;
                        @include transition(all, .4s);
                    }
                    &:nth-of-type(1) {
                        width:14.27%; height:23.32%; top:28.63%; left:2.8125%;
                        &:after { /*background:url(../../images/main/A_generation_over.jpg) 0 0 no-repeat;*/}
                        .plus_btn {top:30%;left:56%;}
                    }
                    &:nth-of-type(2) {
                        width:18.07%; height:26.41%; top:32.2%;left:27.45%;
                        &:after { /*background:url(../../images/main/A_generation_over.jpg) 50% 50% no-repeat;*/}
                        .plus_btn {top:30%;left:60%;}
                    }
                    &:nth-of-type(3) {
                        width:27.03%; height:35.35%; top:49.32%; left:35.93%;
                        &:after {/* background:url(../../images/main/C_generation_over.jpg) 50% 50% no-repeat;*/}
                        .plus_btn { top:44%;left:55%;}
                    }
                    &:nth-of-type(4) {
                        width:21.40%; height:28.86%; top:34.51%; left:75.15%;
                        &:after { /*background:url(../../images/main/C_generation_over.jpg) 50% 50% no-repeat;*/}
                        .plus_btn { top:53%;left:56%;}
                    }
                    &:nth-of-type(5) {
                        width:23.17%; height:25.55%; top:28.24%; left:56.51%;
                        &:after { /*background:url(../../images/main/C_generation_over.jpg) 50% 50% no-repeat;*/}
                        .plus_btn { top:26%;left:55%;}
                    }
                }
                .plus_btn {
                    display:block; width:70px; height:70px; background-color:$gold; font-size:32px; color:$white;
                    font-weight:$extraBold; position:absolute; z-index:1; padding:20px 0; text-align:center;
                    @include boxSizing;
                    @include borderRadius(70px);
                    em {
                        width:100%; height:100%;position:absolute;left:0; top:0; background-color:$white; z-index:2; opacity:0;
                        @include transition(all, .4s);
                        @include borderRadius(100%);
                        &:after, &:before {
                            content:"";display:block;position:absolute; top:50%; left:50%;overflow:hidden;
                            @include transform(translate(-50%,-50%));
                        }
                        &:after {width:4px; height:24px;background-color:$gold;}
                        &:before {width:24px; height:4px;background-color:$gold;}
                    }
                }
                .sub_info {
                    width:230px; height:306px; text-align:left; background-color:$navy;
                    position:absolute; top:50%; left:50%; opacity:0;
                    @include transition(all, .5s);
                    @include transform(translate(15px,-50%));
                    strong { display:block; padding:40px 0 0 40px; font-size:72px; color:#d2ad83; }
                    i { position:relative; left:7px; top:-3px; font-size:18px; color:#d2ad83; }
                    span {
                        display:block; font-size:15px; font-family:$NanumSquare;
                        @include transform(skew(-0.03deg));
                    }
                    p { padding:15px 0 0; font-size:16px; font-weight:$regular; }
                    .bottom_txt { text-align:right; position:absolute; bottom:40px; right:30px; }
                }
            }
        }
        .txt_wrap {
            width:1580px; position:absolute; bottom:100px; left:50%;
            @include transform(translate(-50%, 0));
            h1 { display:block; font-size:54px; color:$white; font-weight:$regular; padding:30px 0 0; }
            p { font-size:14px; color:$white; }
        }
    }
    .sec6 {
        width:100%; background-color:$navy;
        img { width:103px; height:103px; padding:170px 0 0; }
        .txt_wrap {
            width:950px; margin:0 auto; text-align:center; padding:0 0 220px;
            p { padding:50px 0 0; color:$goldYellow; font-size:24px; }
            h1 { padding:30px 0 75px; font-size:50px; color:$white; }
            span {
                position:relative; color:$white; font-size:18px; display:block; width:fit-content; margin:0 auto;
                &:after, &:before { content:"-"; display:block; position:absolute; top:0; }
                &:after { right:-17px; }
                &:before { left:-17px; }
            }
            strong { display:block; padding:35px 0 0; font-size:72px; color:$goldYellow; }
        }
    }
}











.sub1 {
    width:100%; height:3000px; position:relative; background-color:#eee;
    p { position:absolute; right:300px; top:300px; font-size:100px; }
    .box {
        width:500px; height:500px; position:absolute; top:900px; left:50%; overflow:hidden;
        &.box2 { width:500px; height:500px; top:1200px; left:10%; }
    }
    .button_wrap {
        position:absolute; left:50%; background-color:#ccc; top:0;
        span {
            position:absolute; top:48%; left:50%;
            @include transform(translate(-50%, -50%));
        }
        button {
            &:hover {
                span { color:red; }
                svg {
                    @include transform(rotate(90deg));
                    path { stroke-dashoffset:0; }
                }
            }
        }
        svg {
            width:80px; height:80px;
            @include transition(all, .8s);
            path {
                width:100%; stroke-dasharray:390; stroke-dashoffset:-390;
                @include transition(all, .8s);
            }
        }
    }
}

